import { createContext, FC, ReactElement, useContext, useMemo } from 'react';
import { SummaryArticle, SummaryPart, useSelector } from '@topwrite/common';
import useArticle from '@/lib/use-article';

interface ItemProps {
    item: SummaryArticle;
    active: boolean;
    activeDescendant: boolean;
    disabled: boolean;
    children: ReactElement<any, any> | null;
}

export interface CatalogProps {
    renderPart: (props: PartProps) => ReactElement<any, any> | null;
    renderItem: (props: ItemProps) => ReactElement<any, any> | null;
}

interface PartProps {
    part: SummaryPart;
    children: ReactElement<any, any> | null;
}

const CatalogCtx = createContext<CatalogProps>({
    renderPart: () => null,
    renderItem: () => null
});

const Item = ({ item }: { item: SummaryArticle }) => {

    const { path: current } = useSelector('file');
    const article = useArticle();
    const { renderItem } = useContext(CatalogCtx);

    const activeDescendant = useMemo(() => {
        return Boolean(article && item.getLevel().isDescendant(article.getLevel()));
    }, [item, article]);

    const active = current === item.getPath();
    const hasChildren = item.children.length > 0;

    const disabled = !item.getPath();

    const children = hasChildren ? <Items items={item.children} /> : null;

    return renderItem({ item, active, disabled, activeDescendant, children });
};

const Items: FC<{ items: SummaryArticle[] }> = ({ items }) => {
    return <ul>
        {items.map((item) => <Item key={item.level.toString()} item={item} />)}
    </ul>;
};


const Part = ({ part }: { part: SummaryPart }) => {

    const { renderPart } = useContext(CatalogCtx);

    const children = <Items items={part.articles} />;

    return renderPart({ part, children });
};

const Parts = ({ parts }: { parts: SummaryPart[] }) => {
    if (parts.length === 1 && !parts[0].title) {
        return <Items items={parts[0].articles} />;
    }

    return <>{parts.map((part) => {
        return <Part key={part.level.toString()} part={part} />;
    })}</>;
};

export default function Catalog({ ...props }: CatalogProps) {
    const { summary } = useSelector('book');

    return <CatalogCtx.Provider value={props}>
        <Parts parts={summary.parts} />
    </CatalogCtx.Provider>;
}
