import { FC, useMemo } from 'react';
import { InjectedComponent, useBook, useSelector, useThemeConfig } from '@topwrite/common';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import useTitle from '@/lib/use-title';
import { ArticleContext } from '@/lib/use-article';

const Main: FC = () => {
    const { summary } = useBook();
    const file = useSelector('file');
    const title = useTitle();
    const { primaryColor } = useThemeConfig();

    const article = useMemo(() => {
        return summary.getArticle(article => article.getPath() === file.path);
    }, [summary, file]);

    const vars = `
:root {
    --ttw-primary-color: ${primaryColor};
}
`;

    return <HelmetProvider>
        <Helmet>
            <title>{`${article ? `${article.title} - ` : ''}${title}`}</title>
            <style>{vars}</style>
        </Helmet>
        <ArticleContext.Provider value={article}>
            <InjectedComponent role={'app'} />
        </ArticleContext.Provider>
    </HelmetProvider>;
};

export default Main;
