import { useSelector } from '@topwrite/common';

interface LogoProps {
    className?: string
    size?: number
}

export default function Logo({ size = 60, className }: LogoProps) {

    const file = useSelector('file');
    const logo = file.relative('logo.png');

    return <img className={className} src={logo} width={size} height={size} alt='logo' />;
}
