import { ActionsType, Model, File as FileType } from '@topwrite/common';

class File extends Model<FileType> {

    onAction(action: GeneratorFunction, type: string, { update }: ActionsType<File>): any {
        return function *(...args: any[]) {
            const result = yield action(...args);
            if (result && type === 'page/fetch') {
                update(result.file);
                requestAnimationFrame(() => {
                    window.scrollTo(0, 0);
                });
            }
        };
    }

    *update(article: FileType) {
        yield this.setState(() => {
            return new FileType(article);
        });
    }
}

export const file = new File();
