import { createBrowserHistory, createMemoryHistory, Listener, To, History as H } from 'history';

const SUPPORTED = (
    typeof window !== 'undefined' &&
    window.history && window.history.pushState && window.history.replaceState &&
    !navigator.userAgent.match(/((iPod|iPhone|iPad).+\bOS\s+[1-4]\D|WebApps\/.+CFNetwork)/) &&
    window.location.protocol !== 'file:'
);

export class History {

    history: H;

    constructor() {
        this.history = SUPPORTED ? createBrowserHistory() : createMemoryHistory({
            initialEntries: [window.location]
        });
    }

    get location() {
        return this.history.location;
    }

    listen(listener: Listener) {
        this.history.listen(listener);
    }

    replace(location: To) {
        return this.history.replace(location);
    }

    push(location: To) {
        return this.history.push(location);
    }
}

export let history: History;

export function createHistory() {
    return history = new History();
}
