import { AnchorHTMLAttributes } from 'react';
import { useBook } from '@topwrite/common';
import path from 'path';
import Link from '@/components/link';

interface AnchorProps extends AnchorHTMLAttributes<HTMLAnchorElement> {
    base?: string
}

const Anchor = ({ base, href, ...props }: AnchorProps) => {

    const { summary } = useBook();

    if (href) {
        if (href.match(/^https?:\/\//)) {
            props.target = '_blank';
        } else if (path.extname(href) === '.md') {
            let ref;
            if (base) {
                ref = decodeURIComponent(path.join(path.dirname(base), href));
            } else {
                ref = href;
            }

            const article = summary.getArticle(article => article.ref === ref);
            if (article) {
                return <Link to={article} {...props} />;
            }
        }
    }
    return <a href={href} {...props} rel='noopener noreferrer' />;
};

export default Anchor;
