import { useIntl } from '@topwrite/common';

interface PoweredByProps {
    className?: string
}

export default function PoweredBy({ className }: PoweredByProps) {

    const intl = useIntl();

    return <div className={className}>
        {intl.formatMessage(
            { defaultMessage: `本文档使用 {link} 构建` },
            {
                link: <a href="https://x.topthink.com" target="_blank">
                    {intl.formatMessage({
                        defaultMessage: '顶想云'
                    })}
                </a>
            }
        )}
    </div>;
}
